.heading-1 {
	@apply font-sans;
	font-size: clamp(2rem, 5vw, 4rem);
	line-height: 1.125;
}

.heading-2 {
	@apply font-sans;
	font-size: clamp(1.75rem, 4vw, 2.5rem);
	line-height: 1.4;
}

.heading-3 {
	@apply font-sans;
	font-size: clamp(1.5rem, 4vw, 2rem);
	line-height: 2;
}

.heading-4 {
	@apply font-sans;
	font-size: clamp(1.25rem, 4vw, 1.5rem);
	line-height: 1.35;
}

.heading-5 {
	@apply font-sans;
	font-size: 0.875rem;
	letter-spacing: 0.2em;
}

.heading-6 {
	@apply font-sans;
	font-size: 0.875rem;
	letter-spacing: 0.2em;
}

.p {
	@apply font-sans;
	letter-spacing: 0.04em;
	line-height: 2;
}

.p-base {
	font-size: clamp(1rem, 3vw, 1.125rem);
}

.p-small {
	font-size: clamp(0.875rem, 3vw, 1rem);
}
